<template>
  <div class="upload-container">
    <el-upload class="upload-demo"
      action=""
      :limit="2"
      accept=".zip"
      :before-upload="beforeUpload"
      :file-list="FileList"
      :on-success="(response, file, fileList) => {
          handleChange(response, file, fileList,scope)
      }"
      
      >
      <el-button size="small" type="primary" v-loading="loading"></el-button>
      <el-button size="small" type="primary"></el-button>
      <div slot="tip" class="el-upload__tip">shapefile.zip</div>
    </el-upload>
  </div>
</template>
<script>
import shp from 'shpjs/dist/shp';
// :auto-upload="false"
export default {
  name: "loadLocalShp",
  props: {},
  data() {
      return {
        loading:false,
        annexHttpRequest:[],
        FileList: []
      }
  },
  beforeMount(){
    
  },
  mounted() {

  },
  beforeDestroy(){

  },
  destroyed(){ 
        
  },
  methods: { 
    // 
    beforeUpload(file){
      let fileName = file.name.split(".")[1];
      if (fileName != "zip") {
        this.$message.error("zip!");
        return;
      }
    }, 
    handleRemove(){

    },
    // 
    handleChange(file, fileList) {
      // console.log('file:', file, this.fileList);
      this.FileList = fileList;
      // let fileName = file.name.split(".")[1];
      // if (fileName === "zip") {
      //   console.log(file.name);
      //   // this.loading = true;
      //   this.generateFeature(file.raw);
      //   console.log('file.zip:', file);
      // } 
    },
    // 
    generateFeature(file){
      // 
      console.log('file:', file);
      var reader = new FileReader();
      //
      reader.readAsArrayBuffer(file);
      reader.onload =  (result)=> {
      var fileData = result; //fileData
      shp(fileData).then(function (geojson) {
        console.log('geojson:', geojson);
      });
      }
      // this.addShapefileToMap();
    },
    // 
    addShapefileToMap(feature) {
      this.loading = false;
    }
    
  }
}
</script>

